<template>
    <div class="page-index">
        <van-list
        v-if="data"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        >
            <ul>
                <li v-for="(item,key) in data" :key="key">
                    <div class="flex justify-space-between" style="width:100%">
                        <div class="left">
                            <div class="avatar" > <img :src="item.avatar" > <img class="isvip" src="../../assets/images/icon_isvip.png"  v-if="item.is_ident_icon == 1" > </div>
                            <div class="text">
                                <p class="username" >{{item.author}}</p>
                                <div class="flex flex-none"><p class="level" v-bind:class="{'isCarvip':item.is_carvip == 1}">{{item.level}}</p><p class="extcredits">{{item.extcredits2}}°</p></div>
                            </div>
                        </div>
                        <div class="right">
                            <p class="price">¥{{item.question_price}}</p>
                            <p class="time">{{item.dateline | getFromNom()}}</p>
                        </div>
                    </div>
                    <div class="message">
                        <p>{{item.message}}</p>
                    </div>
                    <div class="image-box" v-if="item.attachment == 2 " >
                        <img :class="{'one-image':item.image_list && item.image_list.length == 1 ? true : false}" :src="url"  v-for="(url,key_1) in item.image_list"  :key="key_1" @click="handlePreviewImage(item.image_list,key_1 )" />
                    </div>
                    <div class="poll">
                        <div class="title flex justify-space-between">
                            <span>投票选项</span>
                            <span class="expiration" v-if="item.poll.is_expiration == 1" >投票已结束</span>
                            <p class="countdown" v-if="item.poll.is_expiration != 1" ><span class="text">据截止还有</span><span class="time"><van-count-down :time="item.poll.expiration|countdown()" /></span></p>
                        </div>
                        <ul>
                            <li v-for="(pollitem,key_1) in item.polloption" :key="key_1" >
                                <div class="flex  left"><img :src="pollitem.icon" /><p>{{pollitem.polloption}}</p></div>
                                <div class="right">{{pollitem.votes}}票
                                    <div class="is-vote" v-if="pollitem.polloptionid == item.poll.is_vote"><div></div></div>
                                </div>
                                
                            </li>
                        </ul>
                    </div>
                    <div class="other flex justify-space-between">
                        <div class="time">{{item.dateline | getFromNom()}}</div>
                        <div class="icon flex">
                                <div class="views">{{item.views}}</div>
                                <div class="replies">{{item.replies}}</div>
                                <div class="share"></div>
                        </div>
                    </div>
                </li>
            </ul>
        </van-list>
    </div>
</template>
<script>

import {formatTimestamp,getFromNom} from '@js/moment'
import { ImagePreview } from 'vant';
export default {
    props:['uid','type'],
    data(){
        return {
            data:'',
            loading:false,
            finished:false,
            pageInfo:{
                page_size:10,
                page_index:0
            }
        }
    },
    created(){
        this.getMyThread(this.uid);
    },
    methods:{
        onLoad(){
            this.getMyThread(this.uid);
        },
        handlePreviewImage(v,key){
            ImagePreview({
                images:v,
                startPosition:key,
                loop:false
            })
        },        
        getMyThread(uid){
            this.api.getMyThread({
                token:this.$store.state.userData,
                uid:uid,
                type:2,
                special:1,
                page_size:this.pageInfo.page_size,
                page_index:this.pageInfo.page_index
            }).then(res => {
                this.loading = false;
                if(res.err_code == 0){
                    this.pageInfo.page_index++;
                    if(!res.data.my_thread_data.length){
                        this.finished = true;
                    }
                    this.data = [...this.data,...res.data.my_thread_data] 
                }
            })
        },
    },
    filters:{
        timeformat(v){
            return formatTimestamp(v*1000,'MM-DD')
        },
        getFromNom(v){
            return getFromNom(v*1000)
        },
        countdown(v){
            return v*1000 - new Date().getTime()
           
        }
    }    
}
</script>
